body {
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #1f1f22;
  color: #ffffff;
  overflow-x: hidden; }

body,
*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto;
  -o-object-fit: contain;
     object-fit: contain; }

h1 {
  color: #ffffff;
  text-align: center;
  margin-bottom: 40px; }

main {
  display: flex;
  flex-direction: column;
  gap: 40px;
  flex-grow: 1; }

section {
  border-radius: 20px;
  background-color: #1f1f22;
  box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.1), 4px 4px 12px rgba(0, 0, 0, 0.8); }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.section-title {
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 26px;
  font-weight: 700;
  padding-top: 30px;
  margin-bottom: 20px;
  text-align: center;
  color: #ffffff; }

.footer {
  padding: 30px;
  margin-top: auto;
  text-align: center;
  color: #ffffff; }

.container {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px; }

.flex {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px; }

.flex__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse;
  align-items: flex-end;
  justify-content: space-between;
  align-content: flex-end; }

.flex__item {
  width: 25%;
  padding: 20px;
  margin: 30px 0;
  display: flex;
  justify-content: center;
  align-items: center; }
  .flex__item:nth-of-type(6) {
    align-self: flex-start;
    margin: 0; }

.flex__item img {
  margin: auto;
  padding: 20px;
  box-shadow: -2px -2px 4px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8);
  border-radius: 10px; }
  .flex__item img:hover {
    transition: all 0.5s ease;
    box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8), inset -4px -4px 14px rgba(255, 255, 255, 0.05), inset 4px 4px 14px rgba(0, 0, 0, 0.5); }

.inline-block {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px; }

.inline-block__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.inline-block__item {
  display: inline-block;
  padding: 20px;
  border-radius: 10px;
  width: 25%; }
  .inline-block__item img {
    margin: auto;
    padding: 20px;
    box-shadow: -2px -2px 4px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8);
    border-radius: 10px; }
    .inline-block__item img:hover {
      transition: all 0.5s ease;
      box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8), inset -4px -4px 14px rgba(255, 255, 255, 0.05), inset 4px 4px 14px rgba(0, 0, 0, 0.5); }

.float {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px; }

.float__list {
  list-style: none;
  margin: 0;
  padding: 0; }
  .float__list--clearfix::after {
    content: "";
    display: table;
    clear: both; }

.float__item {
  float: left;
  padding: 20px;
  border-radius: 10px;
  width: 25%; }
  .float__item img {
    margin: auto;
    padding: 20px;
    box-shadow: -2px -2px 4px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8);
    border-radius: 10px; }
    .float__item img:hover {
      transition: all 0.5s ease;
      box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8), inset -4px -4px 14px rgba(255, 255, 255, 0.05), inset 4px 4px 14px rgba(0, 0, 0, 0.5); }

.table {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px; }

.table__table {
  border-collapse: collapse;
  border: none;
  border-spacing: unset; }

.table__cell {
  padding: 20px;
  border-radius: 10px;
  width: 25%; }
  .table__cell img {
    margin: auto;
    padding: 20px;
    box-shadow: -2px -2px 4px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8);
    border-radius: 10px; }
    .table__cell img:hover {
      transition: all 0.5s ease;
      box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8), inset -4px -4px 14px rgba(255, 255, 255, 0.05), inset 4px 4px 14px rgba(0, 0, 0, 0.5); }

.div {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px; }

.div__table {
  display: table; }

.div__row {
  display: table-row; }

.div__cell {
  display: table-cell;
  float: left;
  padding: 20px;
  border-radius: 10px;
  width: 25%; }
  .div__cell img {
    margin: auto;
    padding: 20px;
    box-shadow: -2px -2px 4px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8);
    border-radius: 10px; }
    .div__cell img:hover {
      transition: all 0.5s ease;
      box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8), inset -4px -4px 14px rgba(255, 255, 255, 0.05), inset 4px 4px 14px rgba(0, 0, 0, 0.5); }

.accordion {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px;
  padding-bottom: 40px;
  border-radius: 20px;
  background-color: #1f1f22;
  box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.1), 4px 4px 12px rgba(0, 0, 0, 0.8);
  color: rgba(255, 255, 255, 0.1); }

.accordion__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  gap: 20px; }

.accordion__item {
  box-shadow: -2px -2px 4px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8);
  border-radius: 10px;
  padding: 10px 20px; }
  .accordion__item:hover {
    color: #f1a5f1;
    transition: all 0.5s ease;
    box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.8), inset -2px -2px 10px rgba(255, 255, 255, 0.05), inset 2px 2px 10px rgba(0, 0, 0, 0.5); }
    .accordion__item:hover .accordion__title::before,
    .accordion__item:hover .accordion__title::after {
      background-color: #f1a5f1;
      transition: 0.3s ease; }

.accordion__title {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative; }
  .accordion__title::after {
    position: absolute;
    content: "";
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    width: 20px;
    height: 4px;
    background-color: #4b4f5a;
    margin-left: auto;
    transition: .1s ease; }
  .accordion__title::before {
    position: absolute;
    content: "";
    top: 50%;
    right: 18px;
    transform: translateY(-50%);
    width: 4px;
    height: 20px;
    background-color: #4b4f5a;
    margin-left: auto;
    transition: .1s ease; }
  .accordion__title--active::before {
    display: none; }

.accordion__text {
  margin: 0;
  padding: 0;
  padding-right: 40px;
  max-height: 0;
  overflow: hidden;
  transition: .1s ease max-height; }
